import React from "react";
import Particles from "react-particles-js";
import Typed from "react-typed";

const Home = () => {
  return (
    <div className="home " style={{ "background-attachment": "fixed" }}>
      <Typed
        className=" font-weight-bolder d-flex text-white justify-content-center align-middle text-monospace sayantan"
        strings={["Hi, I'm  Sayantan Ghosh"]}
        typeSpeed={60}
      />
      <Typed
        className="font-italic d-flex text-warning justify-content-center align-middle text-monospace skill mt-4"
        strings={["I build things for Web and Android"]}
        typeSpeed={50}
      />

      <Particles
        style={{ opacity: 0.45 }}
        className="particle"
        params={{
          particles: {
            number: {
              value: 42,
            },
            size: {
              value: 2.5,
            },
          },

          interactivity: {
            events: {
              onhover: {
                enable: true,
                mode: "repulse",
              },
            },
          },
        }}
      >
        <h1 className="text-white">Say</h1>
      </Particles>
    </div>
  );
};
export default Home;
