import React from "react";
import leetcode from "./leetcode.png";
import hackerrank from "./hackerrank.png";
import codechef from "./codechef.png";
const FontAwesome = require("react-fontawesome");
const Contact = () => {
  return (
    <div className="Contact mb-1">
      <h4 className="text-white font-weight-bolder ml-2 my-5">Contact</h4>
      <div className="border round border-warning">
        <ul className="list-group">
          <li className="list-group-item  bg-transparent mt-3 mb-1 ">
            <span className=" mr-1 p-2">
              <FontAwesome
                className="mt-2"
                name="fas fa-google"
                size="2x"
                style={{ color: "white", padding: "1px" }}
              />
            </span>

            <span className="text-warning">gsayantan01@gmail.com</span>
          </li>
          <li className="list-group-item mb-3 bg-transparent">
            <span className=" mr-1 p-2">
              <FontAwesome
                className="mt-2"
                name="fas fa-phone-square"
                size="2x"
                style={{ color: "white", padding: "1px" }}
              />
            </span>
            <span className="text-warning">+91-9831516058</span>
          </li>
        </ul>
        <div className="d-flex justify-content-center align-middle">
          <div className="row  d-flex justify-content-between align-middle">
            <div className="col-xs-3 mx-2">
              <a href="https://www.linkedin.com/in/werfree" target="_blank">
                <FontAwesome
                  name="fab fa-linkedin"
                  size="2x"
                  style={{ color: "white", padding: "1px" }}
                />
              </a>
            </div>

            <div className="col-xs-3 mx-2">
              <a href="https://www.leetcode.com/werfree" target="_blank">
                <img
                  class="card-img-top"
                  style={{ height: "31px", width: "28px", padding: "1px" }}
                  src={leetcode}
                  alt="Leetcode"
                />
              </a>
            </div>
            <div className="col-xs-3 mx-2">
              <a href="https://www.hackerrank.com/werfree" target="_blank">
                <img
                  class="card-img-top"
                  style={{ height: "36px", width: "37px", padding: "1px" }}
                  src={hackerrank}
                  alt="Hackerrank"
                />
              </a>
            </div>
            <div className="col-xs-3 mx-2">
              <a href="https://www.codechef.com/users/werfree" target="_blank">
                <img
                  class="card-img-top"
                  style={{
                    height: "40px",
                    width: "50px",
                    marginBottom: "5px",
                  }}
                  src={codechef}
                  alt="Codechef"
                />
              </a>
            </div>
            <div className="col-xs-3 mx-2">
              <a href="https://www.github.com/werfree" target="_blank">
                <FontAwesome
                  name="fab fa-github"
                  size="2x"
                  style={{ color: "white", padding: "1px" }}
                />
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
export default Contact;
