import React, { useState } from "react";
import { Carousel } from "react-bootstrap";
import problemSolving from "./problemSolving(hackerRank).png";
const Certification = () => {
  const [index, setIndex] = useState(0);
  const banner = () => {
    const handleSelect = (selectedIndex, e) => {
      setIndex(selectedIndex);
    };
    return (
      <Carousel activeIndex={index} onSelect={handleSelect}>
        <Carousel.Item className="" style={{}}>
          <div className="d-flex text-white justify-content-center align-middle">
            <div className="card border-warning mb-3">
              <img
                class="card-img-top"
                src={
                  "https://s3-ap-southeast-1.amazonaws.com/learnyst/schools/2410/certificates/50332/476232_50332.png"
                }
                alt="Certificate"
              />
              <div className="card-body text-warning">
                <h5 className="card-title mb-3 font-weight-bold">
                  Full Stack MERN Bootcamp
                </h5>
                <p>
                  <i>Learncode Online</i>
                </p>
                <div className=" justify-content mb-3 mt-5">
                  <a
                    href="https://github.com/werfree/Drive-Safe"
                    target="_blank"
                  >
                    <span
                      className="ml-5 text-warning border border-warning rounded
                      p-2"
                      style={{ fontSize: "18px" }}
                    >
                      View Certificate
                    </span>
                  </a>
                </div>
              </div>
            </div>

            <Carousel.Caption></Carousel.Caption>
          </div>
        </Carousel.Item>
        <Carousel.Item className="" style={{}}>
          <div className="d-flex text-white justify-content-center align-middle">
            <div className="card border-warning mb-3">
              <img
                class="card-img-top"
                src={problemSolving}
                alt="Certificate"
              />
              <div className="card-body text-warning">
                <h5 className="card-title mb-3 font-weight-bold">
                  Problem Solving (Intermediate)
                </h5>
                <p>
                  <i>HackerRank</i>
                </p>
                <div className=" justify-content mb-3 mt-5">
                  <a
                    href="https://www.hackerrank.com/certificates/de95838e50f8"
                    target="_blank"
                  >
                    <span
                      className="ml-5 text-warning border border-warning rounded
                      p-2"
                      style={{ fontSize: "18px" }}
                    >
                      View Certificate
                    </span>
                  </a>
                </div>
              </div>
            </div>

            <Carousel.Caption></Carousel.Caption>
          </div>
        </Carousel.Item>
        <Carousel.Item className="" style={{}}>
          <div className="d-flex text-white justify-content-center align-middle">
            <div className="card border-warning mb-3">
              <img
                class="card-img-top"
                src={
                  "https://s3-ap-southeast-1.amazonaws.com/learnyst/schools/2410/certificates/12537/476232_12537.png"
                }
                alt="Certificate"
              />
              <div className="card-body text-warning">
                <h5 className="card-title mb-3 font-weight-bold">
                  Android Development with Java
                </h5>
                <p>
                  <i>Learncode Online</i>
                </p>
                <div className=" justify-content mb-3 mt-5">
                  <a
                    href="https://courses.learncodeonline.in/learn/certificate/476232-12537"
                    target="_blank"
                  >
                    <span
                      className="ml-5 text-warning border border-warning rounded
                      p-2"
                      style={{ fontSize: "18px" }}
                    >
                      View Certificate
                    </span>
                  </a>
                </div>
              </div>
            </div>

            <Carousel.Caption></Carousel.Caption>
          </div>
        </Carousel.Item>
      </Carousel>
    );
  };
  return (
    <div className="about">
      <h4 className="text-white font-weight-bolder ml-3 mb-5">Certification</h4>
      {banner()}
    </div>
  );
};
export default Certification;
