import React, { useState } from "react";
import Home from "./Home";
import About from "./About";
import Experence from "./Experence";
import Education from "./Education";
import Project from "./Project";
import Awards from "./Awards";
import Contact from "./ContactMe";
import Certification from "./Certification";
import { Navbar, Nav } from "react-bootstrap";
import scrollToComponent from "react-scroll-to-component";
import Footers from "./Footers";

const Base = () => {
  const [experience, setExperience] = useState("");
  const [home, setHome] = useState("");
  const [about, setAbout] = useState("");
  const [education, setEducation] = useState("");
  const [project, setProject] = useState("");
  const [award, setAward] = useState("");
  const [contact, setContact] = useState("");
  const [certification, setCertification] = useState("");

  return (
    <div className="base  m-1 p-1">
      <Navbar
        sticky="top"
        className="bg-transparent menu"
        bg="dark"
        expand="lg"
        collapseOnSelect="true"
      >
        <Navbar.Brand className="text-white font-weight-bold" href="#home">
          <h6 className="text-warning   mt-2">H O W D Y !</h6>
        </Navbar.Brand>
        <Navbar.Toggle
          className="nav border bg-warning  mr-2"
          aria-controls="basic-navbar-nav"
        />

        <Navbar.Collapse id="basic-navbar-nav margin  ">
          <Nav className="mr-5 text-rounded mt-1 howdy menubackground mb-3">
            <ul className="navbar-nav text-left bg-transparent mt-1">
              <li className="nav-item mr-5 ml-2">
                <div className="btn nav-link text-warning">
                  <span
                    onClick={() => {
                      scrollToComponent(home, {
                        offset: 0,
                        align: "top",
                        duration: 1500,
                      });
                    }}
                  >
                    Home
                  </span>
                </div>
              </li>
              <li className="nav-item mr-5 ml-2">
                <div className="btn nav-link text-warning">
                  <span
                    onClick={() => {
                      scrollToComponent(about, {
                        offset: 0,
                        align: "middle",
                        duration: 1500,
                      });
                    }}
                  >
                    About
                  </span>
                </div>
              </li>
              <li className="nav-item mr-5 ml-2">
                <div className="btn nav-link text-warning">
                  <span
                    onClick={() => {
                      scrollToComponent(experience, {
                        offset: 0,
                        align: "middle",
                        duration: 1500,
                      });
                    }}
                  >
                    Experience
                  </span>
                </div>
              </li>
              <li className="nav-item mr-5 ml-2">
                <div className="btn nav-link text-warning">
                  <span
                    onClick={() => {
                      scrollToComponent(education, {
                        offset: 0,
                        align: "middle",
                        duration: 1500,
                      });
                    }}
                  >
                    Education
                  </span>
                </div>
              </li>
              <li className="nav-item mr-5 ml-2">
                <div className="btn nav-link text-warning">
                  <span
                    onClick={() => {
                      scrollToComponent(project, {
                        offset: 0,
                        align: "middle",
                        duration: 1500,
                      });
                    }}
                  >
                    Project
                  </span>
                </div>
              </li>
              <li className="nav-item mr-5 ml-2">
                <div className="btn nav-link text-warning">
                  <span
                    onClick={() => {
                      scrollToComponent(award, {
                        offset: 0,
                        align: "top",
                        duration: 1500,
                      });
                    }}
                  >
                    Award
                  </span>
                </div>
              </li>
              <li className="nav-item mr-5 ml-2">
                <div className="btn nav-link text-warning">
                  <span
                    onClick={() => {
                      scrollToComponent(certification, {
                        offset: 0,
                        align: "top",
                        duration: 1500,
                      });
                    }}
                  >
                    Certification
                  </span>
                </div>
              </li>
              <li className="nav-item mr-5 ml-2">
                <div className="btn nav-link text-warning">
                  <span
                    onClick={() => {
                      scrollToComponent(contact, {
                        offset: 0,
                        align: "top",
                        duration: 1500,
                      });
                    }}
                  >
                    Conatct
                  </span>
                </div>
              </li>
            </ul>
          </Nav>
        </Navbar.Collapse>
      </Navbar>

      <section
        className="violet"
        ref={(section) => {
          setHome(section);
        }}
      >
        <Home />
      </section>

      <section
        className="violet"
        ref={(section) => {
          setAbout(section);
        }}
      >
        <About />
      </section>

      <section
        className="violet"
        ref={(section) => {
          setExperience(section);
        }}
      >
        <Experence />
      </section>

      <section
        className="violet"
        ref={(section) => {
          setEducation(section);
        }}
      >
        <Education />
      </section>

      <section
        className="violet"
        ref={(section) => {
          setProject(section);
        }}
      >
        <Project />
      </section>

      <section
        className="violet"
        ref={(section) => {
          setAward(section);
        }}
      >
        <Awards />
      </section>

      <section
        className="violet"
        ref={(section) => {
          setCertification(section);
        }}
      >
        <Certification />
      </section>

      <section
        className="violet"
        ref={(section) => {
          setContact(section);
        }}
      >
        <Contact />
      </section>
      <Footers />
    </div>
  );
};
export default Base;
