import React, { useState } from "react";
import { Carousel } from "react-bootstrap";

const Project = () => {
  const [index, setIndex] = useState(0);
  const banner = () => {
    const handleSelect = (selectedIndex, e) => {
      setIndex(selectedIndex);
    };
    return (
      <Carousel activeIndex={index} onSelect={handleSelect}>
        <Carousel.Item className="">
          <div className="d-flex text-white justify-content-center align-middle">
            <div className="card border-warning mb-3">
              <div className="card-body text-warning">
                <h5 className="card-title mb-3">My-Tees</h5>
                <p className="card-text text-secondary mb-4">
                  An E-Commerce website build on MERN stack with Braintree
                  payment gateway integrated in it.I have use JWT for
                  authentication.
                </p>
                <div className="ml-4 mb-3">
                  <a
                    href="https://github.com/werfree/Ecommerce"
                    target="_blank"
                  >
                    <span
                      className="ml-5 text-warning border border-warning rounded
                      p-2"
                      style={{ fontSize: "18px" }}
                    >
                      View More
                    </span>
                  </a>
                </div>
              </div>
            </div>
            <Carousel.Caption></Carousel.Caption>
          </div>
        </Carousel.Item>
        <Carousel.Item className="">
          <div className="d-flex text-white justify-content-center align-middle">
            <div className="card border-warning mb-3">
              <div className="card-body text-warning">
                <h5 className="card-title mb-3">Life Saver</h5>
                <p className="card-text text-secondary mb-4">
                  A mobile application that can connect a willing blood donor to
                  the recipient of his/her area.
                </p>
                <div className="ml-4 mb-3">
                  <a
                    href="https://github.com/werfree/Android/tree/master/Life_Saver"
                    target="_blank"
                  >
                    <span
                      className="ml-5 text-warning border border-warning rounded
                      p-2"
                      style={{ fontSize: "18px" }}
                    >
                      View More
                    </span>
                  </a>
                </div>
              </div>
            </div>
            <Carousel.Caption></Carousel.Caption>
          </div>
        </Carousel.Item>
        <Carousel.Item className="">
          <div className="d-flex text-white justify-content-center align-middle">
            <div className="card border-warning mb-3">
              <div className="card-body text-warning">
                <h5 className="card-title mb-3">Vote Now</h5>
                <p className="card-text text-secondary mb-4">
                  A mobile application that can be used to conduct any kind of
                  election/vote.It uses{" "}
                  <strong>
                    <i>
                      <u>Real Time Face Recognition</u>
                    </i>
                  </strong>{" "}
                  to validate the voter.
                </p>
                <div className="ml-4 mb-3">
                  <a href="https://github.com/werfree/VoteNow" target="_blank">
                    <span
                      className="ml-5 text-warning border border-warning rounded
                      p-2"
                      style={{ fontSize: "18px" }}
                    >
                      View More
                    </span>
                  </a>
                </div>
              </div>
            </div>
            <Carousel.Caption></Carousel.Caption>
          </div>
        </Carousel.Item>
        <Carousel.Item className="">
          <div className="d-flex text-white justify-content-center align-middle">
            <div className="card border-warning mb-3">
              <div className="card-body text-warning">
                <h5 className="card-title mb-3">Drive Safe</h5>
                <p className="card-text text-secondary mb-4">
                  A hardware as well as a software solution to reduce death rate
                  by traffic collision in our country. The hardware and the
                  software can do its work independently
                </p>
                <div className="ml-4 mb-3">
                  <a
                    href="https://github.com/werfree/Drive-Safe"
                    target="_blank"
                  >
                    <span
                      className="ml-5 text-warning border border-warning rounded
                      p-2"
                      style={{ fontSize: "18px" }}
                    >
                      View More
                    </span>
                  </a>
                </div>
              </div>
            </div>
            <Carousel.Caption></Carousel.Caption>
          </div>
        </Carousel.Item>
        <Carousel.Item className="">
          <div className="d-flex text-white justify-content-center align-middle">
            <div className="card border-warning mb-3">
              <div className="card-body text-warning">
                <h5 className="card-title mb-3">Recipe</h5>
                <p className="card-text text-secondary mb-4">
                  Search the recipe of your favourite dish with the app.
                </p>
                <div className="ml-4 mb-3">
                  <a href="https://github.com/werfree/Recipe" target="_blank">
                    <span
                      className="ml-5 text-warning border border-warning rounded
                      p-2"
                      style={{ fontSize: "18px" }}
                    >
                      View More
                    </span>
                  </a>
                </div>
              </div>
            </div>
            <Carousel.Caption></Carousel.Caption>
          </div>
        </Carousel.Item>
      </Carousel>
    );
  };
  return (
    <div className="about">
      <h4 className="text-white font-weight-bolder ml-3 mb-5">Projects</h4>
      {banner()}
    </div>
  );
};
export default Project;
