import React from "react";
import myPic from "./myPic.jpg";

const About = () => {
  return (
    <div className="about">
      <h4 className="text-white font-weight-bolder ml-3 mb-5">About Me</h4>
      <div className="row">
        <div className="col-md-4 d-flex justify-content-center">
          <img
            className="image border border-warning p-1 rounded"
            src={myPic}
            alt="Pic"
          />
        </div>
        <div className="col-md-7 mx-1 my-4 text-secondary">
          <p>
            Hello! I'm Sayantan, a B.Tech (CSE) Student at RCC Institute of
            Information Technology. I am an Android as well as a MERN Stack
            Developer.
          </p>
          <p>
            Here are a few technologies I've been working with: <br />
            <div className="row mt-4 mr-1 ml-1">
              <div className="col ">
                <strong>
                  <i>React Native</i>
                </strong>
              </div>
              <div className="col ">
                <strong>
                  <i>Native Android</i>
                </strong>
              </div>
            </div>
            <div className="row mr-1 ml-1">
              <div className="col ">
                <strong>
                  <i>React Js</i>
                </strong>
              </div>
              <div className="col ">
                <strong>
                  <i>Node JS</i>
                </strong>
              </div>
            </div>
            <div className="row mr-1 ml-1">
              <div className="col ">
                <strong>
                  <i>Python 3</i>
                </strong>
              </div>
              <div className="col ">
                <strong>
                  <i>Selenium | bs4</i>
                </strong>
              </div>
            </div>
          </p>
        </div>
      </div>
    </div>
  );
};
export default About;
